"use client"

import { useSession } from "next-auth/react"
import { useRouter } from "next/navigation"
import { useEffect, useState } from "react"

interface AuthGuardProps {
  children: React.ReactNode
  requiredRole?: "USER" | "ADMIN" | "SUPER_ADMIN"
  fallback?: React.ReactNode
}

export default function AuthGuard({ 
  children, 
  requiredRole = "USER",
  fallback 
}: AuthGuardProps) {
  const { data: session, status } = useSession()
  const router = useRouter()
  const [shouldRender, setShouldRender] = useState(false)

  useEffect(() => {
    console.log('üîí AuthGuard Debug:', {
      status,
      hasSession: !!session,
      userRole: session?.user?.role,
      requiredRole,
      shouldRender,
      timestamp: new Date().toISOString()
    })

    // Se est√° carregando, aguarda
    if (status === "loading") {
      console.log('üîí AuthGuard: Loading...')
      return
    }

    // Se temos sess√£o v√°lida, verifica permiss√µes
    if (status === "authenticated" && session) {
      const roleHierarchy = {
        USER: 1,
        ADMIN: 2, 
        SUPER_ADMIN: 3
      }

      const userLevel = roleHierarchy[session.user.role as keyof typeof roleHierarchy] || 0
      const requiredLevel = roleHierarchy[requiredRole]

      if (userLevel >= requiredLevel) {
        console.log('üîí AuthGuard: Access granted, rendering content')
        setShouldRender(true)
        return
      } else {
        console.log('üîí AuthGuard: Insufficient permissions')
        router.push("/unauthorized")
        return
      }
    }

    // Se chegou aqui e n√£o est√° autenticado, aguarda um pouco
    // O middleware pode estar protegendo a rota
    const timer = setTimeout(() => {
      if (status === "unauthenticated" || !session) {
        console.log('üîí AuthGuard: Not authenticated after timeout, redirecting')
        router.push("/login")
      }
    }, 2000) // Aguarda 2 segundos para dar tempo da sess√£o hidratar

    return () => clearTimeout(timer)
  }, [session, status, router, requiredRole])

  // Loading state
  if (status === "loading" || !shouldRender) {
    console.log('üîí AuthGuard: Rendering loading state')
    return fallback || (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center">
          <h1 className="text-2xl font-bold mb-4">Carregando...</h1>
          <div className="animate-spin rounded-full h-32 w-32 border-b-2 border-gray-900 mx-auto"></div>
        </div>
      </div>
    )
  }

  console.log('üîí AuthGuard: Rendering protected content')
  return <>{children}</>
} 